module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">children</td><td style=\"padding-bottom: 1.8rem\">node</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Enables the ability to add custom content to doc template. Children will be rendered after all other content.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">packageName</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The given component&#39;s npm package name.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">packageUrl</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The url for the provided package if not in npm. This will be ignored if packageVersion is not set.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">packageVersion</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The given component&#39;s package version.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">readme</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The given component&#39;s readme file imported to a string.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">srcPath</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The url to the source code for the given component.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">examples</td><td style=\"padding-bottom: 1.8rem\">An array of objects structured like:<br /><pre style=\"white-space: pre-wrap\">{\n \"name\": \"shape\",\n \"value\": {\n  \"title\": {\n   \"name\": \"string\",\n   \"required\": false\n  },\n  \"description\": {\n   \"name\": \"node\",\n   \"required\": false\n  },\n  \"example\": {\n   \"name\": \"element\",\n   \"required\": false\n  },\n  \"source\": {\n   \"name\": \"string\",\n   \"required\": false\n  }\n }\n}</pre></td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">[]</td><td style=\"padding-bottom: 1.8rem\">All of the example(s) that will be displayed. An empty array is supported.<pre><code style=\"white-space: pre-wrap\">title: The title of the example\ndescription: A description of the example to be displayed below the title\nexample: The example to be displayed\nsource: The source code of the example</code></pre></td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">propsTables</td><td style=\"padding-bottom: 1.8rem\">An array of objects structured like:<br /><pre style=\"white-space: pre-wrap\">{\n \"name\": \"shape\",\n \"value\": {\n  \"componentSrc\": {\n   \"name\": \"string\",\n   \"required\": false\n  },\n  \"componentName\": {\n   \"name\": \"string\",\n   \"required\": false\n  },\n  \"componentProps\": {\n   \"name\": \"string\",\n   \"required\": false\n  },\n  \"propsResolution\": {\n   \"name\": \"string\",\n   \"required\": false\n  }\n }\n}</pre></td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">[]</td><td style=\"padding-bottom: 1.8rem\">All of the props table(s) that will be displayed. An empty array is supported.<pre><code style=\"white-space: pre-wrap\">componentSrc: The source code of the component\ncomponentName: The name of the component\npropsResolution: Type of react-docgen resolver to use for prop-types resolution. Supported values are `default` or `findAllComponentDefinitions`. Will use `default` if left unspecified.</code></pre></td></tr></tbody></table>"